<template>
  <div>
    <UnitsTable
      ref="unitsTable"
      @handle-edit="(id) => $refs.sidebarUnit.getElement(id)"
    >
      <template v-slot:plus>
        <b-button
          variant="primary"
          @click="$refs.sidebarUnit.open()"
        >
          <span class="text-nowrap">Registrar Nuevo</span>
        </b-button>
      </template>
    </UnitsTable>
    <SidebarUnit
      ref="sidebarUnit"
      @reload="$refs.unitsTable.fetchList()"
    />
  </div>
</template>

<script>
import { ref } from '@vue/composition-api'
import { BButton } from 'bootstrap-vue'
import UnitsTable from './components/UnitsTable.vue'
import SidebarUnit from './components/SidebarUnit.vue'

export default {
  components: {
    BButton,
    UnitsTable,
    SidebarUnit,
  },
  setup() {
    const sidebarUnit = ref()
    const devicesTable = ref()
    return {
      devicesTable,
      sidebarUnit,
    }
  },
}
</script>
