import { useToast } from 'vue-toastification/composition'
import ToastificationContentVue from '@/@core/components/toastification/ToastificationContent.vue'
import { ref, reactive, watch } from '@vue/composition-api'
import _ from 'lodash'
import UnitService from '@/services/UnitService'

const useUnitsTable = (perPage = 10) => {
  const title = 'Unidades'
  const toast = useToast()
  const isLoading = ref(false)
  const units = ref([])

  const perPageOptions = [10, 25, 50]

  const headerColumns = [
    { key: 'id', label: '#', sortable: true },
    { key: 'plate', label: 'Placas', sortable: true },
    { key: 'model.name', label: 'Modelo', sortable: true },
    { key: 'brand.name', label: 'Marca', sortable: true },
    {
      key: 'devices',
      label: 'Dispositivos',
      sortable: false,
      stickyColumn: true,
      isRowHeader: true,
      variant: 'primary',
      // tdClass: (value, key, item) => (item.devices != null ? 'table-primary' : 'table-danger'),
    },
    {
      key: 'driver',
      label: 'Conductor',
      sortable: false,
      stickyColumn: true,
      isRowHeader: true,
      variant: 'success',
      tdClass: (value, key, item) => (item.driver != null ? 'table-success' : 'table-danger'),
    },
    { key: 'createdAt', label: 'Fecha de creación', sortable: true },
    { key: 'updatedAt', label: 'Fecha de modificación', sortable: true },
    { key: 'status', label: 'Estatus', sortable: true },
    { key: 'actions', label: 'Acciones' },
  ]

  const listQuery = reactive({
    take: perPage,
    page: 1,
    order: 'ASC',
    pageCount: 1,
    itemCount: 0,
    search: '',
    sortBy: 'id',
    from: 0,
    to: 0,
  })

  const fetchList = async () => {
    isLoading.value = true
    try {
      const response = await UnitService.fetchList(listQuery)
      const { data, meta } = response.data
      const {
        take,
        page,
        pageCount,
        itemCount,
        from,
        to,
      } = meta
      listQuery.itemCount = Number(itemCount)
      listQuery.pageCount = Number(pageCount)
      listQuery.take = Number(take)
      listQuery.from = Number(from)
      listQuery.to = to > data.length ? itemCount : to
      listQuery.page = page
      units.value = data
      isLoading.value = false
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContentVue,
        props: {
          title: `Error al cargar ${title}`,
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
    }
  }

  const resolveStatus = status => (status ? 'success' : 'danger')

  const rowClass = (item, type) => {
    let colorClass = ''
    if (item || type === 'row') {
      if (item.device !== null) {
        colorClass = 'table-success'
      } else {
        colorClass = 'table-danger'
      }
      return colorClass
    }
    return colorClass
  }

  const parseDevices = devicesList => {
    const devices = devicesList.map(device => device.name)
    return devices.join(', ')
  }

  const reload = _.debounce(() => {
    fetchList()
  }, 500)

  watch(() => listQuery.take, () => fetchList())

  watch(() => listQuery.page, () => fetchList())

  watch(() => listQuery.search, () => {
    reload()
  })

  fetchList()

  return {
    title,
    perPageOptions,
    units,
    headerColumns,
    isLoading,
    listQuery,
    resolveStatus,
    fetchList,
    rowClass,
    parseDevices,
  }
}
export default useUnitsTable
