import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@/@core/components/toastification/ToastificationContent.vue'
import UnitService from '@/services/UnitService'
import { reactive, ref } from '@vue/composition-api'

const useUnit = () => {
  const toast = useToast()
  const isSaving = ref(false)
  const isLoading = ref(false)
  const blank = {
    id: '',
    plate: '',
    model: '',
    devices: [],
    brand: '',
    driver: '',
    status: true,
  }
  const states = reactive({
    plate: {
      isInvalid: false,
      message: '',
    },
  })
  const isSidebarActive = ref(false)

  const unit = reactive(blank)

  const resetData = () => {
    unit.id = ''
    unit.plate = ''
    unit.status = true
    unit.devices = []
    unit.driver = ''
    unit.model = ''
    unit.brand = ''

    states.plate.isInvalid = false
    states.plate.message = ''
  }

  const saveNew = async () => {
    isSaving.value = true
    try {
      const { id, devices, ...rest } = unit
      await UnitService.create({ ...rest, devices: devices.map(d => d.id) })
      isSaving.value = false
      isSidebarActive.value = false
      setTimeout(() => {
        resetData()
      }, 1000)
      toast({
        component: ToastificationContent,
        props: {
          title: 'Registrado correctamente!!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
      return true
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.plate.isInvalid = true
        states.plate.message = error.response.data.message
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Registrar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const edit = async id => {
    isLoading.value = true
    try {
      const response = await UnitService.getById(id)
      const {
        plate,
        status,
        devices,
        driver,
        brand,
        model,
      } = response.data
      unit.id = response.data.id
      unit.plate = plate
      unit.status = status
      // unit.devices = devices ? devices.map(d => d.id) : []
      unit.devices = devices
      unit.driver = driver ? driver.id : ''
      unit.model = model.id
      unit.brand = brand.id
      isLoading.value = false
      isSidebarActive.value = true
    } catch (error) {
      isLoading.value = false
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Consultar registro',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const update = async () => {
    isSaving.value = true
    try {
      const { id, devices, ...rest } = unit
      const devicesList = devices.map(d => d.id)
      await UnitService.update(unit.id, { devices: devicesList, ...rest })
      isSaving.value = false
      isSidebarActive.value = false
      setTimeout(() => {
        resetData()
      }, 1000)
      toast({
        component: ToastificationContent,
        props: {
          title: 'Actualizado Correctamente!',
          icon: 'CheckCircleIcon',
          variant: 'success',
        },
      })
    } catch (error) {
      isSaving.value = false
      if (error.response.data.statusCode === 400) {
        states.plate.isInvalid = true
        states.plate.message = error.response.data.message
        console.log({ states })
      }
      toast({
        component: ToastificationContent,
        props: {
          title: 'Error al Actualizar',
          icon: 'AlertTriangleIcon',
          variant: 'danger',
        },
      })
      throw error
    }
  }

  const save = async () => {
    if (unit.id !== '') {
      await update()
    } else {
      await saveNew()
    }
  }

  return {
    isSaving,
    isSidebarActive,
    states,
    unit,
    // methods
    save,
    resetData,
    edit,
    update,
  }
}
export default useUnit
