<template>
  <b-sidebar
    id="sidebar"
    sidebar-class="sidebar-lg"
    :visible="isSidebarActive"
    bg-variant="white"
    shadow
    backdrop
    no-header
    right
    @hidden="()=> {
      resetForm(),
      resetData()
    }"
    @change="(val) => isSidebarActive = val"
  >
    <template #default="{ hide }">
      <div
        class="d-flex justify-content-between align-items-center content-sidebar-header px-2 py-1"
      >
        <h5 class="mb-0">
          {{ unit.id ? "Editar" : "Registrar" }} Unidad
        </h5>

        <feather-icon
          class="ml-1 cursor-pointer"
          icon="XIcon"
          size="16"
          @click="hide"
        />
      </div>

      <!-- BODY -->
      <validation-observer
        #default="{ handleSubmit }"
        ref="refFormObserver"
      >
        <b-form
          ref="form"
          class="p-2"
          @submit.prevent="handleSubmit(onSubmit)"
        >
          <b-alert
            v-if="states.plate.isInvalid"
            show
            variant="danger"
          >
            <div class="alert-body">
              <feather-icon
                class="mr-25"
                icon="AlertCircleIcon"
              />
              <span class="ml-25">{{ states.plate.message }}</span>
            </div>
          </b-alert>
          <validation-provider
            #default="validationContext"
            name="plate"
            rules="required"
          >
            <b-form-group
              :state="getValidationState(validationContext)"
              label="Placa"
              label-for="plate"
              :invalid-feedback="states.plate.mensaje"
            >
              <b-form-input
                id="plate"
                v-model="unit.plate"
                placeholder="placa"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>
          <validation-provider
            #default="validationContext"
            name="model"
            rules="required"
          >
            <b-form-group
              label="Modelo"
              label-for="model"
            >
              <v-select
                v-model="unit.model"
                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                :options="mList"
                :reduce="(val) => val.id"
                label="name"
                :clearable="false"
                input-id="model"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>
          <validation-provider
            #default="validationContext"
            name="brand"
            rules="required"
          >
            <b-form-group
              label="Marca"
              label-for="brand"
            >
              <v-select
                v-model="unit.brand"
                :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
                :options="bList"
                :reduce="(val) => val.id"
                label="name"
                :clearable="false"
                input-id="brand"
              />
              <b-form-invalid-feedback
                :state="getValidationState(validationContext)"
              >
                {{ validationContext.errors[0] }}
              </b-form-invalid-feedback>
            </b-form-group>
          </validation-provider>
          <b-form-group
            label="Dispositivos"
            label-for="device"
          >
            <v-select
              v-model="unit.devices"
              multiple
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="devicesList"
              label="name"
              :clearable="true"
              input-id="device"
            />
          </b-form-group>
          <b-form-group
            label="Conductor"
            label-for="driver"
          >
            <v-select
              v-model="unit.driver"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              :options="driversList"
              :reduce="(val) => val.id"
              :clearable="true"
              input-id="driver"
              :get-option-label="getFullName"
            />
          </b-form-group>
          <b-form-group
            label="Activo"
            label-for="status"
          >
            <b-form-checkbox
              v-model="unit.status"
              class="custom-control-success"
              name="status"
              switch
            />
          </b-form-group>
          <b-button
            v-ripple.400="'rgba(255, 255, 255, 0.15)'"
            variant="primary"
            class="mr-2 mt-4"
            type="submit"
            block
          >
            <feather-icon
              icon="SaveIcon"
              class="mr-50 mt-25"
            />
            <b-spinner
              v-if="isSaving"
              small
            />
            <span class="align-middle ml-1">
              {{ unit.id ? "Actualizar" : "Registrar " }}
            </span>
          </b-button>
        </b-form>
      </validation-observer>
    </template>
  </b-sidebar>
</template>

<script>
import {
  BAlert,
  BSidebar,
  BForm,
  BFormCheckbox,
  BFormGroup,
  BFormInput,
  BFormInvalidFeedback,
  BButton,
  BSpinner,
} from 'bootstrap-vue'
import { ValidationProvider, ValidationObserver, localize } from 'vee-validate'
import { required } from '@validations'
import formValidation from '@core/comp-functions/forms/form-validation'
import Ripple from 'vue-ripple-directive'
import vSelect from 'vue-select'
import useCatalogs from '@/composables/useCatalogs'
import { computed } from '@vue/composition-api'
import useUnit from '../composables/useUnit'

localize({
  en: {
    messages: {
      // generic rule messages...
    },
    fields: {
      plate: {
        required: 'Introduce un número de placa',
      },
      modelo: {
        required: 'Selecciona un modelo',
      },
      brand: {
        required: 'Selecciona un marca',
      },
    },
  },
})

export default {
  components: {
    BAlert,
    BSpinner,
    BSidebar,
    BForm,
    BFormCheckbox,
    BFormGroup,
    BFormInput,
    BFormInvalidFeedback,
    BButton,
    vSelect,

    // Form Validation
    ValidationProvider,
    ValidationObserver,
  },
  directives: {
    Ripple,
  },
  setup(__, { emit }) {
    const resetuserData = () => emit('reset-data')

    const { refFormObserver, getValidationState, resetForm } = formValidation(resetuserData)

    const {
      devicesList,
      getDevices,
      // getDevicesExcept,

      modelsList,
      getModels,

      brandsList,
      getBrands,

      driversList,
      getDrivers,
      getDriversExcept,

    } = useCatalogs()

    const {
      unit,
      isSaving,
      isSidebarActive,
      save,
      edit,
      resetData,
      states,
    } = useUnit()

    const mList = computed(() => (unit.id ? modelsList.value.filter(m => m.status || m.id === unit.model)
      : modelsList.value.filter(e => e.status)))

    const bList = computed(() => (unit.id ? brandsList.value.filter(b => b.status || b.id === unit.brand)
      : brandsList.value.filter(e => e.status)))

    const onSubmit = () => {
      save().then(() => {
        emit('reload')
      })
    }

    const reloadCatalogs = async () => {
      getBrands()
      getModels()
    }

    const reloadCatalogsRegister = async () => {
      reloadCatalogs()
      await getDevices()
      await getDrivers()
    }

    const open = async () => {
      await reloadCatalogsRegister()
      isSidebarActive.value = true
    }

    const getElement = async id => {
      reloadCatalogs()
      await edit(id)
      await getDevices()
      if (unit.driver) {
        await getDriversExcept(unit.driver)
      } else {
        await reloadCatalogsRegister()
      }
    }

    const getFullName = driver => `${driver.name} ${driver.lastName}`

    return {
      unit,
      isSaving,
      isSidebarActive,
      states,

      mList,
      bList,
      devicesList,
      driversList,

      onSubmit,
      refFormObserver,
      getValidationState,
      resetForm,
      open,
      getElement,
      resetData,
      getFullName,

      required,
    }
  },
}
</script>

<style lang="scss">
@import "@core/scss/vue/libs/vue-select.scss";

#add-new-user-sidebar {
  .vs__dropdown-menu {
    max-height: 200px !important;
  }
}
</style>
